.chart {
    flex: 4;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
    padding: 10px;
    color: gray;
    background-color: #fff;
  }
  
  .chart .title {
    margin-bottom: 20px;
  }
  
  .chart .chartGrid {
    stroke: rgb(228, 225, 225);
  }
  .charts{
    display: flex;
    gap: 20px;
    padding: 20px 0px 20px 0px;
  }